import _ from 'lodash'
import { ObjectUnsubscribedError } from 'rxjs';

export function getVariant(data, keys)
{
    let s_variant = {};

    data.map((variant, key) => {
        let flags=[];

        // console.log('Keys :: ' + JSON.stringify(keys))
        keys.map((key) => {
            if(key && Object.keys(key).length > 0)
            {
                if(_.find(variant['Attributes'], {'value': key}))
                {
                    return flags.push(true);
                }
                else
                {
                    return flags.push(false);
                }
            }
        })

        if(flags.every(checkFlag))
        {
           return s_variant = {variant: variant,
                key: key
            };
            // return(variant);
        }
        
    })

    return s_variant;
}

function checkFlag(flag)
{
    return flag === true;
}
