import _ from 'lodash'

export function getCollectionAttribute(variants){
    let collectionAttributes = [];

    variants.map((variant) => {
        let uniqAttribute = _.uniqBy(variant['Attributes'],'cat');

        return uniqAttribute.map((item) => {
            let obj = {};
            obj[item.cat] = item.value;
           return collectionAttributes.push(obj);
        })
    })

    // console.log('Collection Array :: ' + JSON.stringify(collectionAttributes));
    let sortedArray = _.uniqWith(collectionAttributes, _.isEqual)
    // console.log('Sorted Array :: ' + JSON.stringify(sortedArray));

    // getColorsCollection(sortedArray);
    return sortedArray;
}

export function getImageCollection(variants)
{
    let imageCollection = [];
    // console.log("Variants :: " + JSON.stringify(variants))
    variants.map((variant) => {
        let images = {};
        if(variant['Image'] && variant['Attributes'])
        {     
            let colorName = '';

            variant['Attributes'].map((att) => {
                if(att.cat === 'Color')
                {
                    colorName=att.value;
                    return att;
                }
            })

            if(variant['Image'] && colorName)
            {               
                               
                images[colorName] = variant['Image'];
                let index = _.findIndex(imageCollection, (img) => {return Object.keys(img)[0] === colorName})

                if(index && index === -1)
                {
                    return imageCollection.push(images);
                }
                else if(index > -1)
                {
                    // console.log('Value :: ' + JSON.stringify(imageCollection[index]));
                    // console.log('Already Value :: ' + imageCollection[index][colorName]);
                    // console.log('Value to add :: ,' + variant['Image']);
                    let newString = (imageCollection[index][colorName]).concat(','+variant['Image'])
                    return imageCollection[index][colorName] = newString
                    // console.log('After Concat :: ' + newString);

                    // Object.keys(imageCollection[index][colorName])[0] += ','+variant['Image']
                    // let modedArr = Array.prototype.join( [...newArr, variant['Image']], ',')
                    // console.log('Image Collectiton ' + imageCollection[index][colorName]);
                    // console.log('Image Object ' + variant['Image']);
                    // imageCollection[index][colorName] = modedArr
                }
            }
            else if(variant['Image'])
            {
                imageCollection.push(variant['Image'])
            }
        }   
    })


    // imageCollection.map((img) => {

    //     console.log(Object(img))
    // })

    // console.log('Images Collection :: ' + JSON.stringify(imageCollection));

    return _.uniq(imageCollection)

    // return imageCollection;
}

export function getVariantAttribute(variant)
{
    let attribute = [];
    // console.log('Atts :: ' + variant);
    let attributes = _.uniqBy(variant && variant['Attributes'],'cat');

    // console.log('Attributes :: ' + JSON.stringify(attributes))

    let colorAtt, sizeAtt, ageAtt = ''

    if(attributes)
    attributes.map(att => {
        if(att.cat === 'Color')
        {
            colorAtt = att.value
        }
        else if (att.cat === 'Age')
        {
            ageAtt = att.value
        }
        else if (att.cat === 'Size')
        {
            sizeAtt = att.value
        }
    })

    // if(attributes)
    // attributes.map((att) => {
    //     let obj = {};
    //     obj[att.cat] = att.value;
    //     return attribute.push(obj);
    // })

    // if(attribute.length > 0)
    // return {color: attribute[0].Color ? attribute[0].Color : '', size: attribute[1].Size ? attribute[1].Size:'', age: attribute[1].Age ? attribute[1].Age:'' }
    return { color: colorAtt ? colorAtt : null, 
            size: sizeAtt ? sizeAtt : null,
            age: ageAtt ? ageAtt : null}

}