import React, { Component } from "react";
import { Nav } from "react-bootstrap";
import Product from "./Product";

import CartPopup from "../CartPopup";
import _ from "lodash";

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class ProductList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      layout: true,
      cartToggle: false,
      productList: null,
    };
  }

  componentWillUnmount() {
    this.setState({
      productList: [],
    });
  }

  static getDerivedStateFromProps(props, state) {
    return {
      productList: props.productList,
    };
  }

  handleClick = () => {
    this.setState({
      layout: !this.state.layout,
    });
  };

  showCartPopup = () => {
    this.setState({ cartToggle: true });
  };

  handleClose = () => {
    this.setState({ cartToggle: false });
  };

  handleShowCart = (product, pro_qty) => {
    this.props.saveDataToCart(product, pro_qty);
    this.setState({ cartToggle: true });
  };

  handleSort = (event) => {
    // alert('selected :: ' + event.target.getAttribute('sortValue'))
    this.props.handleSortProduct(event.target.getAttribute("sortValue"));

    // if(this.state.productList && Object.keys(this.state.productList).length > 0)
    // {
    //    let pro = _.sortBy(this.state.productList, ['Name']);
    //    console.log('Pro :: ' + JSON.stringify(pro))
    //    this.setState({
    //        productList : pro
    //    }, () => {
    //     console.log('After Pro :: ' + JSON.stringify(this.state.productList))
    //    })
    // }
  };

  render() {
    let products = this.state.productList;

    const productFilter = [
      {
        name: "New Arrivals",
        value: "new-arrivals",
        link: "#new_arrivals",
      },
      // {
      //     name: 'Best Seller',
      //     link: '#a'
      // },
      {
        name: "Discount",
        value: "discount",
        link: "#discount",
      },
      {
        name: "Price",
        value: "price",
        link: "#price",
      },
      {
        name: "Name",
        value: "name",
        link: "#name",
      },
    ];

    return (
      <section id="product-list">
        <div className="product-filter">
          <div className="item">
            <span className="title">SORT BY :</span>
            <div className="items">
              {productFilter.map((a) => (
                <div className="d-flex">
                  <Nav.Link
                    href={a.link}
                    sortValue={a.value}
                    onClick={this.handleSort}
                  >
                    {a.name}
                  </Nav.Link>
                </div>
              ))}
            </div>
          </div>
          <div className="product-layout">
            <div
              onClick={this.handleClick}
              className={
                this.state.layout
                  ? "list-view layout-bg"
                  : "detail-view layout-bg"
              }
              title={this.state.layout ? "List" : "Detail"}
            ></div>
          </div>
        </div>

        {Array.isArray(products) && (
          <div id="products-display">
            <div className={this.state.layout ? "grid grid4" : "grid grid5"}>
              {Object.keys(products).length > 0 ? (
                products.map((pro) => (
                  <Product
                    product={pro}
                    addToCartAction={this.handleShowCart}
                  />
                ))
              ) : (
                <h4 style={{ color: "red" }}>
                  {" "}
                  Sorry ! No products found... :/
                </h4>
              )}
            </div>
          </div>
        )}

        <CartPopup
          handleShow={this.handleShow}
          handleClose={this.handleClose}
          show={this.state.cartToggle}
        />
      </section>
    );
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      saveDataToCart: Actions.saveDataToCart,
    },
    dispatch
  );
};

export default withReducer(
  "productReducer",
  reducer
)(connect(null, mapDispatchToProps)(ProductList));
// export default ProductList
