import React, { Component } from 'react';
import {Modal, Button} from 'react-bootstrap';
import QuickView from './QuickView';

import {getVariant} from './product/utilitiesComponent/VariantUtility'
import {getVariantAttribute} from './product/utilitiesComponent/AttributeUtility'


class ViewPopup extends Component {

    constructor(props)
    {
      super(props);

      this.state={
        color: null,
        size: null,
        age:null
      }
    }

    static getDerivedStateFromProps(props, state) {
      // console.log('View popup console ');
      let variant = {key:0};
      let firstVariant = {};

      if((props.product && props.product['Type'] === 'varient') && ((state.color || (state.size || state.age)) && props.variantProduct === state.variantProduct))
      {
        variant = getVariant(props.product['Varients'], [state.color && state.color,state.size && state.size, state.age && state.age]);
        return {
            product: props.product,
            variant_key: variant.key,
        }
      }
      else if((props.product && props.product['Type'] === 'varient') && !(state.color && state.size && state.age))
      {
        firstVariant = getVariantAttribute(props.product && props.product['Varients'][0]);
        // console.log('Heloooo :: ');
        return {
          product: props.product,
          variant_key: variant.key,
          color: firstVariant && firstVariant.color,
          size: firstVariant && firstVariant.size,
          age: firstVariant && firstVariant.age
        }
      }
 
    }

    handleColor = (color) => {
      this.setState({
          color: color
      })
    }

    handleSize = (size) => {
        this.setState({
            size: size
        })
    }

    handleAge = (age) => {
        this.setState({
            age: age
        })
    }

    handleShowCart = (product, pro_qty) => {
      this.props.saveToCart(product,pro_qty);
      this.setState({ cartToggle: true });
    }

    render() {
        return (
        <>

            <Modal 
              className="cust-modal"
              show={this.props.show} 
              onHide={this.props.handleClose}
              size="lg"
            >
            
            <div className="btn-close">
              <Button
                type="button" 
                className="close"
                onClick={this.props.handleClose}
                ><span aria-hidden="true">×</span><span className="sr-only">Close</span>
              </Button>
            </div>

            <Modal.Body>
              {/* <iframe className="embed-responsive-item" src={"http://localhost:3000/quickview/" + this.props.id} title=" "></iframe> */}
              <QuickView 
                  pro={this.props.product['Type'] === 'simple' ? this.props.product : this.props.product['Varients'][this.state.variant_key]} 
                  product={this.props.product}
                  color={this.state.color} handleColor={this.handleColor}
                  size={this.state.size} handleSize={this.handleSize}
                  age={this.state.age} handleAge={this.handleAge}
                  addToCartAction={this.handleShowCart}
              /> 
            </Modal.Body>
            </Modal>

        </>
        )
    }
}

export default ViewPopup;
