import React, { Component } from 'react';
import { Row, Col, OverlayTrigger, Tooltip,  Button } from 'react-bootstrap';
import {Link} from 'react-router-dom'
import ReactImageMagnify from 'react-image-magnify';
import Slider from "react-slick";
import { hostUrl } from './store/config';
import _ from 'lodash'

//... REDUX ...
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

// ... UTILITIES ...

import {getCollectionAttribute,getImageCollection} from './product/utilitiesComponent/AttributeUtility'

import {
  getLocalStorageObject,
  productFoundInShortlist
} from './Utilities'

class QuickView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: this.props.product_id,
      value: 1,
      data: [],
      selColor: null,
      nav1: null,
      nav2: null,
      shortlist: false,
      imgID:0
    };
  }

  componentWillMount() {
    // this.saveData();
  }

  static getDerivedStateFromProps(props, state) {
    console.log('Color :: ' + props.color);
    let images = null;
    if(props.product && props.product['Type'] === 'simple')
    {
        images = props.product.Image
        console.log('Images :: ' + JSON.stringify(props.product.Image.split(',')));
    }
    else
    {
        images = getImageCollection(props.product && props.product['Varients'])
    }

    return {
      images: images,
      pro: props.pro,
      product: props.product,
      color: props.color,
      size: props.size,
      age:props.age
    }
  }

  decrease = () => {
    if (this.state.value && this.state.value > 1) {
      this.setState({
        value: this.state.value - 1
      });
    }
  }

  increase = () => {
    this.setState({ value: this.state.value + 1 });
  }

  findProductById = (data, idToLookFor) => {
    for (var i = 0; i < Object.keys(data).length; i++) {
      if (data[i].Id === parseInt(idToLookFor)) {
        return (data[i]);
      }
    }
  }

  saveData = () => {
    return (
      this.setState({
        // data: this.findProductById(Product.data, this.state.id)
      })
    )
  }

  // componentDidMount() {
  //   this.setState({
  //     nav1: this.slider1,
  //     nav2: this.slider2
  //   });
  // }

  hoverImgEnter = () => {
    this.refs.proZoom.style.display = "block";
  }
  
  hoverImgLeave = () => {
    this.refs.proZoom.style.display = "none";
  }

  handleColor = (event) => {
    console.log('Color :: ' + event.target.getAttribute('color'));
    
    this.props.handleColor(event.target.getAttribute('color'));
  }

  handleSize = (event) => {
    console.log('Size :: ' + event.target.getAttribute('sizeName'));

    this.props.handleSize(event.target.getAttribute('sizeName'));

  }

  handleAge = (event) => {
    console.log('Age :: ' + event.target.getAttribute('age'));

    this.props.handleAge(event.target.getAttribute('age'));
  }

  handleImg = (event) => {
    console.log('event Image :: ' + event.target.getAttribute('imgID'));
    this.setState({
      imgID: event.target.getAttribute('imgID')
    })
    // this.props.handleImg(event.target.getAttribute('imgID'));
}


 
  render() {

    let attributes= null;
    if(this.state.product && this.state.product['Type'] === 'varient')
    {
      attributes = getCollectionAttribute(this.state.product['Varients']);
    }
    
    const currentCookies = getLocalStorageObject('shortlist');
    const foundInCookies = productFoundInShortlist(this.props.pro && this.props.pro.Id, currentCookies);

    return (
      <div className="bg-white">
        <Row md={5} className="pro-view m-0">
          <Col md={5} className="pro-img-slider">
            
          <Slider
          asNavFor={this.state.nav2}
          ref={
            slider => (this.slider1 = slider)

          }
          arrows={false}
          pauseOnHover={true}
          autoplay={false}
          autoplaySpeed= {4000}
        >
          {this.props.product && this.props.product['Type'] === 'simple' ?
            this.state.images && (this.state.images.split(',')).map((img, key) => {
              return(parseInt(this.state.imgID) === key) &&
              <div>
                <div 
                  className="pro-img-slide"
                  onMouseEnter={this.hoverImgEnter} 
                  onMouseLeave={this.hoverImgLeave} 
                >
                  <ReactImageMagnify
                    enlargedImagePosition="beside"
                    enlargedImagePortalId= "portal"
                    shouldUsePositiveSpaceLens="true"
                    imageClassName="s-img"
                  {...{
                    smallImage: {
                        alt: 'London',
                        width: 242,
                        height: 294,
                        // src: `/assets/images/products/270x327/${this.state.data.Id + img}.jpg`,
                        src:`${hostUrl}${img}`
                    },
                    largeImage: {
                        // src: `/assets/images/products/1600x1939/${this.state.data.Id + img}.jpg`,
                        src:`${hostUrl}${img}`,
                        width: 1600,
                        height: 1939,
                    },
                    enlargedImageContainerDimensions: {
                      width: 400,
                      height: 400
                    },
                    imageStyle:{
                      width: "242px", height: "294px"}
                  }} />
                </div>
                
              </div>
            })
            :
            this.state.images &&
              this.state.images.map((item) => {
                  return (
                    this.state.color ? item[this.state.color] &&
                    (item[this.state.color].split(',')).map((img, key) => 
                      {
                        return(parseInt(this.state.imgID) === key) &&
                        <div>
                          <div 
                            className="pro-img-slide"
                            onMouseEnter={this.hoverImgEnter} 
                            onMouseLeave={this.hoverImgLeave} 
                          >
                            <ReactImageMagnify
                              enlargedImagePosition="beside"
                              enlargedImagePortalId= "portal"
                              shouldUsePositiveSpaceLens="true"
                              imageClassName="s-img"
                            {...{
                              smallImage: {
                                  alt: 'London',
                                  width: 242,
                                  height: 294,
                                  // src: `/assets/images/products/270x327/${this.state.data.Id + img}.jpg`,
                                  src:`${hostUrl}${img}`
                              },
                              largeImage: {
                                  // src: `/assets/images/products/1600x1939/${this.state.data.Id + img}.jpg`,
                                  src:`${hostUrl}${img}`,
                                  width: 1600,
                                  height: 1939,
                              },
                              enlargedImageContainerDimensions: {
                                width: 400,
                                height: 400
                              },
                              imageStyle:{
                                width: "242px", height: "294px"}
                            }} />
                          </div>
                          
                        </div>
                      })
                      :
                      (item && item.split(',')).map((img, key) => 
                      {
                        return(parseInt(this.state.imgID) === key) &&
                        <div>
                          <div 
                            className="pro-img-slide"
                            onMouseEnter={this.hoverImgEnter} 
                            onMouseLeave={this.hoverImgLeave} 
                          >
                            <ReactImageMagnify
                              enlargedImagePosition="beside"
                              enlargedImagePortalId= "portal"
                              shouldUsePositiveSpaceLens="true"
                              imageClassName="s-img"
                            {...{
                              smallImage: {
                                  alt: 'London',
                                  width: 242,
                                  height: 294,
                                  // src: `/assets/images/products/270x327/${this.state.data.Id + img}.jpg`,
                                  src:`${hostUrl}${img}`
                              },
                              largeImage: {
                                  // src: `/assets/images/products/1600x1939/${this.state.data.Id + img}.jpg`,
                                  src:`${hostUrl}${img}`,
                                  width: 1600,
                                  height: 1939,
                              },
                              enlargedImageContainerDimensions: {
                                width: 400,
                                height: 400
                              },
                              imageStyle:{
                                width: "242px", height: "294px"}
                            }} />
                          </div>
                          
                        </div>
                      })
                  )
              })
          }
        </Slider>

        {/* Slider List in Quick view */}
        <Slider
          asNavFor={this.state.nav1}
          ref={slider => (this.slider2 = slider)}
          slidesToShow={this.props.product && this.props.product['Type'] === 'simple' ? 
          (this.state.images) && Object.keys(this.state.images).length > 0 && Object.keys(this.state.images.split(',')).length
          :
          _.compact(this.state.images && this.state.images.map((item) => {
               return (this.state.color && item[this.state.color] && Object.keys(item[this.state.color].split(',')).length)
           }))[0]}
          swipeToSlide={true}
          focusOnSelect={true}
          arrows={false}
          dots={false}
          pauseOnHover={true}
          className="slide-thumb"
        >
           {this.props.product && this.props.product['Type'] === 'simple' ?
              this.state.images && 
              // <div className='d-flex'>
              (this.state.images.split(',')).map((img, key) => {
                  return (
                    <div>
                      <div className="pro-img-thumb">
                          {/* <img src={`/assets/images/products/270x327/${this.state.data.Id + img}.jpg`} alt="" /> */}
                          <img src={`${hostUrl}${img}`} imgID={key}  onClick={this.handleImg} alt="" />
                      </div>
                    </div>
                  )
              })
              // </div>
              :

              this.state.images && 
                    // <div className='d-flex'>

                        (this.state.images.map((item) => 
                            {
                                return(
                                (this.state.color && item[this.state.color]) &&
                                (item[this.state.color].split(',')).map((img, key) => 
                                {
                                    return (
                                      // <div>
                                      <div>
                                        <div className="pro-img-thumb">
                                            {/* <img src={`/assets/images/products/270x327/${this.state.data.Id + img}.jpg`} alt="" /> */}
                                            <img src={`${hostUrl}${img}`} imgID={key}  onClick={this.handleImg} alt="" />
                                        </div>
                                      </div>
                                      // </div>
                                    )
                                }))
                            
                            }
                        ))
                          // </div> 
             
          }
        </Slider>
        <div id="portal" className="pro-img-zoom" ref="proZoom">
        </div>
            <div className="pro-size-chart">
              <a href='#'>
                <img src="/assets/images/size-chart1.png" alt="" />
              </a>
            </div>
            <div className={foundInCookies ? "slp-s-img" : 'slp-img'} onClick={() => {
                  this.props.saveProductToShortlist(this.props.pro)
                  this.setState({
                      shortlist: !this.state.shortlist
                  })
              }}></div>
          </Col>
          <Col md={7} className="pro-details">
            <div className="pro-name">{this.state.pro && this.state.pro.Name}</div>
            <div className="pro-desc">{this.state.product && 
                <div dangerouslySetInnerHTML={{__html: ( this.state.product && this.state.product['Description']).substring(0, 60)+'...'}}/>}
            </div>
            <div className="qtn-stock">
              <div className="qtn-count">
                <div className="stock-status">{this.state.pro && parseInt(this.state.pro["Stock Status"]) === 1 ? "IN STOCK" : "OUT OF STOCK"}</div>
                <div className="qtn-count-container">
                  <div className="qtn-label">QTY:</div>
                  <div className="qtn-inc-dec">
                    <button onClick={this.decrease} className="minus">-</button>
                    <input className="quantity" min="1" name="quantity" value={this.state.value} 
                      type="text" />
                    <button onClick={this.increase} className="plus">+</button>
                  </div>
                </div>
              </div>
              <div className="pro-price-container">
                <div className="pro-offers">
                  {
                    (this.state.pro && this.state.pro["Sale Price"] > 0 ) ? 
                      <>
                        <div className="pro-price">Rs: {this.state.pro['Sale Price']}</div>
                        <div className="pro-disc">{Math.round(((parseInt(this.state.pro['Price']) - parseInt(this.state.pro['Sale Price'])) / this.state.pro['Price'] * 100))}% OFF</div>
                        <div className="pro-offer">
                          <div className="pro-o-price">Rs: {this.state.pro.Price}</div>
                          <div className="pro-disc-price">Discount {this.state.pro.Price - this.state.pro["Sale Price"]}</div>
                        </div>
                      </>
                      :<div className="pro-price">Rs: {this.state.pro.Price}</div>
                  }
                </div>
                <div className="pro-loyality">
                  <span className="loyal-txt">
                    <a href="#a">Earn Loyalty Cash: Rs.2</a>
                  </span>
                  {
                    <OverlayTrigger
                      className="bg-white"
                      overlay={
                        <Tooltip id="tooltip-top">
                          Earn Rs.50 Loyalty Cash and redeem in your future purchases.
                                </Tooltip>
                      }
                    >
                      <span className="loyal-info">?</span>
                    </OverlayTrigger>
                  }
                </div>
              </div>
            </div>
            <div className="delivery-check">
              <div className="delivery-txt">
                <div className="delivery-bg"></div>
                <p>Check Delivery details.
                  {
                    <OverlayTrigger
                      placement="bottom"
                      trigger={['hover', 'click', 'focus']}
                      delay={{ show: 250, hide: 400 }}
                      className="bg-white"
                      overlay={
                        <Tooltip id="tooltip-bottom">
                          <h3>Tentative Delivery</h3>
                          Tentative Delivery is the number of days it will take the carrier to deliver the product to your doorstep. This estimate will be different for each product on site. Sundays and public holidays are not included in this estimate.
                                </Tooltip>
                      }
                    >
                      <span className="info"> [?]</span>
                    </OverlayTrigger>
                  }
                </p>
              </div>
              <div className="delivery-zip">
                <input className="zip" name="zip" type="tel" maxLength="20" placeholder="Enter Zip Code" />
                <input type="submit" value="CHECK" className="btn-check" />
              </div>
            </div>
            <div className="pro-return">
              <div className="pro-return-txt">
                <div className="return-bg"></div>
                <p>30 Days- Free Return.
                  {
                    <OverlayTrigger
                      placement="bottom"
                      trigger="click"
                      delay={{ show: 250, hide: 400 }}
                      className="bg-white"
                      overlay={
                        <Tooltip id="tooltip-bottom">
                          <h3>EASY RETURN POLICY</h3>
                          In case you find any problems, damages or defects in the products you can simply return the product to us within 30 days of receiving the product. On Successful processing of returned Item(s), we will refund the entire amount. Please note that the product should be unused, unwashed with all original tags intact and should be sent back in original packaging with a copy of original invoice.
                                    <br />
                          <a className="pro-return-link" href="#q">View our Return Policy to know more.</a>
                        </Tooltip>
                      }
                    >
                      <span className="info">[?]</span>
                    </OverlayTrigger>
                  }
                </p>
              </div>
            </div>
            <div className="pro-more-detail">
              <Link target="_blank" to={this.state.product && `/product/${this.state.product['Slug']}`}>MORE PRODUCT DETAILS</Link>
            </div>
          </Col>
        </Row>
        <Row className="pop-bottom m-0">
          {(this.state.product && this.state.product['Type'] === 'varient') && 
          <Col md={6} className="pro-filter">
            
            <div className="pro-color-filter">
              <span>COLOR:</span>
              {
                <OverlayTrigger
                  placement="top"
                  trigger={['hover', 'click', 'focus']}
                  delay={{ show: 1000, hide: 2000 }}
                  className="bg-white"
                  overlay={
                    <Tooltip id="tooltip-top-color">
                      <div className="all-choose">
                        <div className="sel-color">
                          {(_.find(attributes, (att) => {
                              return (att['Color'])
                          })) ?
                              attributes.map((att) => {
                                  
                                if(att['Color'])
                                {
                                  return(
                                  <span 
                                    style={{background: att['Color']}}
                                    color={att['Color']}
                                    onClick={this.handleColor}
                                  > </span>
                                  )
                                }
                              })
                            : ''
                      }
                        </div>
                      </div>
                    </Tooltip>
                  }
                >
                  <div className="choose">
                    <div className="sel-color" style={{background: this.state.color }}></div>
                  </div>
                </OverlayTrigger>
              }
            </div>
            <div className="pro-size-filter">
              <span>SIZE:</span>
              {
                <OverlayTrigger
                  placement="top"
                  trigger={['hover', 'click', 'focus']}
                  delay={{ show: 1000, hide: 2000 }}
                  className="bg-white"
                  overlay={
                    <Tooltip id="tooltip-top-size">
                      <div className="all-sizes">
                          <div className="sel-size">
                          {attributes.map((att)=> {
                            if(att['Size'])
                            {
                              return (
                                <div class={this.state.size === att['Size'] ? 'size sel':'size'} sizeName={att['Size']} 
                                    onClick={this.handleSize}
                                >
                                    {att['Size']}
                                </div>
                            )
                            }
                            
                          })} 
                          {/* {this.state.product &&
                            this.state.product.Type === 'varient' ? this.state.product.Varients.map((vari, key) => (
                              vari.Attributes[0].Sizes.map((size) => (
                                <div class="size sel">{size} </div>
                              ))
                            )) : ''
                          } */}
                          </div>
                      </div>
                    </Tooltip>
                  }
                >
                  <div className="choose">
                    <div className="seltd-size">{this.state.size}</div>
                  </div>
                </OverlayTrigger>
              }
            </div>

            <div className="pro-size-filter">
              <span>Age:</span>
              {
                <OverlayTrigger
                  placement="top"
                  trigger={['hover', 'click', 'focus']}
                  delay={{ show: 1000, hide: 2000 }}
                  className="bg-white"
                  overlay={
                    <Tooltip id="tooltip-top-size">
                      <div className="all-sizes">
                          <div className="sel-size">
                          {attributes.map((att)=> {
                            if(att['Age'])
                            {
                              return (
                                <div class={this.state.size === att['Age'] ? 'size sel':'size'} age={att['Age']} 
                                    onClick={this.handleAge}
                                >
                                    {att['Age']}
                                </div>
                            )
                            }
                            
                          })} 
                          {/* {this.state.product &&
                            this.state.product.Type === 'varient' ? this.state.product.Varients.map((vari, key) => (
                              vari.Attributes[0].Sizes.map((size) => (
                                <div class="size sel">{size} </div>
                              ))
                            )) : ''
                          } */}
                          </div>
                      </div>
                    </Tooltip>
                  }
                >
                  <div className="choose">
                    <div className="seltd-size">{this.state.age}</div>
                  </div>
                </OverlayTrigger>
              }
            </div>
          </Col>
          }
          <Col md={6}>
            <div className="btn-cart">
            <Button
                onClick = { () => {
                    this.props.addToCartAction(this.state.pro && this.state.pro, this.state.value || 1)
                    // this.props.updateCart();
                }}
                className="btn-cart"
            >ADD TO CART</Button>
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}


const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    saveProductToShortlist: Actions.saveProductToShortlist
  }, dispatch)
}

export default withReducer('quickView', reducer)
          (
            connect(null, mapDispatchToProps)(QuickView)
          )
// export default QuickView;
