import React, { Component } from "react";
import {
  Nav,
  OverlayTrigger,
  Button,
  Tooltip,
  Tab,
  Row,
  Col,
} from "react-bootstrap";
import Slide from "react-slick";
import ViewPopup from "../ViewPopup";
import Cookies from "universal-cookie";
import Skeleton from "react-loading-skeleton";
import Loading from "./../Loading";
import Img from "react-image";

import {
  productFoundInCache,
  productFoundInShortlist,
  getLocalStorageString,
  removeProductFromCookies,
  getLocalStorageObject,
} from "../Utilities";

import { Link } from "react-router-dom";
import _ from "lodash";
import { hostUrl } from "../store/config";
import CartPopup from "../CartPopup";

import { getVariant } from "./utilitiesComponent/VariantUtility";
import { getVariantAttribute } from "./utilitiesComponent/AttributeUtility";
import { getCollectionAttribute } from "./utilitiesComponent/AttributeUtility";

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class Product extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      color: null,
      age: null,
      size: null,
      activeTab: 0,
      cart: false,
      view: false,
      shortlist: false,
      show: false,
      showCart: false,
      id: "",
      procache: [],
      cookie: [],
    };
    this.handleShow = this.handleShow.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleShowCart = this.handleShowCart.bind(this);
    this.handleCloseCart = this.handleCloseCart.bind(this);
  }

  componentDidMount() {
    this.setState({
      procache: JSON.stringify(getLocalStorageString("procache")),
    });
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('hwdwdllll :: ' +props.location);
    return {
      product: props.product,
      // shortlist: !state.shortlist
    };
  }

  handleColor = (color) => {
    this.setState({ color });
  };

  handleAge = (age) => {
    this.setState({ age });
  };

  handleSize = (size) => {
    this.setState({ size });
  };

  handleClose() {
    this.setState({ show: false });
  }

  handleShow() {
    this.setState({ show: true });
  }

  handleCloseCart = () => {
    this.setState({ cartToggle: false });
  };

  handleShowCart = (product, pro_qty) => {
    this.props.addToCartAction(product, pro_qty);
    this.setState({ show: false });
  };

  render() {
    let product = null;
    if (this.state.product) product = this.state.product;

    let attributes = [];
    if (this.state.product && this.state.product["Type"] === "varient")
      attributes = getCollectionAttribute(
        this.state.product["Varients"].length > 0 &&
          this.state.product["Varients"]
      );

    var settings = {
      dots: false,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      autoplay: true,
      autoplaySpeed: 2000,
      arrows: false,
      pauseOnHover: false,
      fade: true,
      cssEase: "linear",
    };

    const currentCookies = getLocalStorageObject("shortlist");
    const currentCache = getLocalStorageString("procache");
    const foundInLocalCache = productFoundInCache(
      this.state.product && this.state.product["Type"] === "simple"
        ? this.state.product["Id"]
        : this.state.product["Varients"][0]["Id"],
      currentCache
    );

    const foundInCookies = productFoundInShortlist(
      this.state.product && this.state.product["Type"] === "simple"
        ? this.state.product.Id
        : this.state.product["Varients"][0]["Id"],
      currentCookies
    );

    return (
      <>
        <div className="pro-container">
          <div className="pro-item">
            <Tab.Container
              className="allsizes"
              defaultActiveKey="1"
              activeKey={this.state.activeTab}
            >
              <div className="pro-detail">
                <Link
                  to={`/product/${product && product["Slug"]}`}
                  className="pro-img"
                >
                  {/* {console.log('Done key')} */}
                  {product && product["Type"] === "varient" ? (
                    product.Varients.map(
                      (vari, key) =>
                        (this.state.activeTab === 0
                          ? 0
                          : this.state.activeTab) === key ? (
                          // (vari['Image'].split(',')).map((img) => {
                          <Img
                            width="100%"
                            className={
                              foundInLocalCache ||
                              this.state.view === true ||
                              foundInCookies
                                ? "pro-main-img img_fade"
                                : "pro-main-img "
                            }
                            src={hostUrl + vari["Image"].split(",")[0]}
                            alt=""
                            loader={<Loading isProduct={true} />}
                          />
                        ) : (
                          // })
                          ""
                        )
                      // <Img width='100%' className={ (foundInLocalCache || this.state.view === true || foundInCookies) ? 'pro-main-img img_fade' : 'pro-main-img ' } src={hostUrl + (product.Image.split(','))[0]} alt="" loader={<Loading isProduct={true} />}/>
                    )
                  ) : (
                    <Img
                      width="100%"
                      className={
                        foundInLocalCache ||
                        this.state.view === true ||
                        foundInCookies
                          ? "img_fade"
                          : ""
                      }
                      src={
                        this.state.product &&
                        hostUrl + this.props.product.Image.split(",")[0]
                      }
                      alt=""
                      loader={<Loading isProduct={true} />}
                    />
                  )}
                  <div className="hvslideshow">
                    <Slide {...settings}>
                      {product &&
                        product["Type"] === "varient" &&
                        product.Varients[0]["Image"]
                          .split(",")
                          .map((vari, key) => {
                            return (
                              <div className="slide">
                                <Img
                                  width="100%"
                                  className={
                                    foundInLocalCache ||
                                    this.state.view === true ||
                                    foundInCookies
                                      ? "img_fade"
                                      : ""
                                  }
                                  src={hostUrl + vari}
                                  alt=""
                                  loader={<Loading isProduct={true} />}
                                />
                              </div>
                            );
                          })}
                    </Slide>
                  </div>
                  <div
                    className="pro cart"
                    style={{ display: foundInLocalCache ? "block" : "none" }}
                  >
                    <a href="#a">
                      <span>Added to Cart</span>
                    </a>
                  </div>
                  <div
                    className="pro view"
                    style={{
                      display: this.state.view === true ? "block" : "none",
                    }}
                  >
                    <a href="#a">
                      <span>Already Viewed</span>
                    </a>
                  </div>
                  <div
                    className="pro shortlist"
                    style={{ display: foundInCookies ? "block" : "none" }}
                  >
                    <a href="#a">
                      <span>Shortlisted</span>
                    </a>
                  </div>
                </Link>
                <div className="pro-txt mobile">
                  <p>
                    {product && product["Name"]}
                    {/* <div dangerouslySetInnerHTML={{__html: ( product && product['Description']).substring(0, 60)+'...'}}/> */}
                    <span className="pro-shortlist">
                      <a
                        href="#a"
                        onClick={() => {
                          if (product && product["Type"] === "simple") {
                            this.props.saveProductToShortlist(product);
                            this.setState({
                              shortlist: !this.state.shortlist,
                            });
                          } else {
                            this.props.saveProductToShortlist(
                              product["Varients"][0]
                            );
                            this.setState({
                              shortlist: !this.state.shortlist,
                            });
                          }
                        }}
                      >
                        <div className={foundInCookies ? "sl-s-img" : "sl-img"}>
                          {" "}
                        </div>
                        {/* <span>Shortlist</span> */}
                      </a>
                    </span>
                  </p>
                </div>
                <div className="pro-price">
                  <a href="#a">
                    {product && product["Type"] === "simple" ? (
                      product["Sale Price"] === 0 ? (
                        <span className="price">Rs.{product.Price}</span>
                      ) : (
                        <>
                          <span className="price">
                            Rs.{product["Sale Price"]}
                          </span>
                          <span className="o-price">Rs.{product.Price}</span>
                          <span className="disc">
                            {" "}
                            ( <span>UPTO</span>{" "}
                            {Math.round(
                              ((product["Price"] - product["Sale Price"]) /
                                product["Price"]) *
                                100
                            )}
                            % OFF)
                          </span>
                          <div className="pro-txt hover">
                            <p>
                              {product && product["Name"]}
                              {/* <div dangerouslySetInnerHTML={{__html: ( product && product['Description']).substring(0, 60)+'...'}}/> */}
                            </p>
                          </div>
                        </>
                      )
                    ) : product["Varients"][0]["Sale Price"] === 0 ? (
                      <span className="price">
                        Rs.{product["Varients"][0].Price}
                      </span>
                    ) : (
                      <>
                        <span className="price">
                          Rs.{product["Varients"][0]["Sale Price"]}
                        </span>
                        <span className="o-price">
                          Rs.{product["Varients"][0].Price}
                        </span>
                        <span className="disc">
                          {" "}
                          (<span>UPTO</span>{" "}
                          {Math.round(
                            ((product["Varients"][0]["Price"] -
                              product["Varients"][0]["Sale Price"]) /
                              product["Varients"][0]["Price"]) *
                              100
                          )}
                          % OFF)
                        </span>
                      </>
                    )}
                  </a>
                </div>
              </div>
              <div className="pro-hvdetail">
                <div className="pro-color">
                  {product && product["Type"] === "varient" && (
                    <div className="color-section">
                      <Row className="clearfix m-0">
                        <Col sm={12} className="p-0">
                          <Tab.Content
                            activeTab={this.state.activeTab}
                            animation
                          >
                            <Tab.Pane eventKey={this.state.activeTab}>
                              <Row className="clearfix">
                                <Col md={12} sm={12} className="size-list">
                                  {
                                    <span>
                                      {attributes.map((att) => {
                                        if (att["Size"]) {
                                          return (
                                            <>
                                              <a
                                                className={`${
                                                  this.state.size == att["Size"]
                                                    ? "active-size"
                                                    : ""
                                                }`}
                                                onClick={(e) =>
                                                  this.handleSize(att["Size"])
                                                }
                                                sizeName={att["Size"]}
                                              >
                                                {att["Size"]}
                                              </a>
                                            </>
                                          );
                                        } else if (att["Age"]) {
                                          return (
                                            <>
                                              <a
                                                className={`${
                                                  this.state.age == att["Age"]
                                                    ? "active-age"
                                                    : "cursor-pointer"
                                                }`}
                                                onClick={(e) =>
                                                  this.handleAge(att["Age"])
                                                }
                                                sizeName={att["Age"]}
                                              >
                                                {att["Age"]}
                                              </a>
                                            </>
                                          );
                                        }
                                      })}
                                    </span>
                                  }
                                </Col>
                              </Row>
                            </Tab.Pane>
                          </Tab.Content>
                        </Col>
                        <Col sm={12} className="p-0 center-content">
                          <Nav bsStyle="tabs">
                            {_.find(attributes, (att) => {
                              return att["Color"];
                            }) &&
                              attributes.map((att, key) => {
                                if (att["Color"]) {
                                  return (
                                    <div
                                      className={`${
                                        this.state.color == att["Color"]
                                          ? "active-color"
                                          : ""
                                      } bg-color-border`}
                                    >
                                      <Nav.Link
                                        className="bg-color"
                                        style={{
                                          background: `${att["Color"]}`,
                                        }}
                                        onClick={(e) =>
                                          this.handleColor(att["Color"])
                                        }
                                        eventKey={key}
                                      ></Nav.Link>
                                    </div>
                                  );
                                }
                              })}
                          </Nav>
                        </Col>
                      </Row>
                    </div>
                  )}
                </div>
                <div className="pro-loyality">
                  <span className="loyal-txt">
                    <a href="#a">Earn Loyalty Cash: Rs.2</a>
                  </span>
                  {
                    <OverlayTrigger
                      overlay={
                        <Tooltip id="tooltip-top">
                          Earn Rs.50 Loyalty Cash and redeem in your future
                          purchases.
                        </Tooltip>
                      }
                    >
                      <span
                        className="loyal-info"
                        title="Earn Rs.50 Loyalty Cash and redeem in your future purchases."
                      >
                        ?
                      </span>
                    </OverlayTrigger>
                  }
                </div>
                <div className="pro-price hover">
                  <a href="#a">
                    {product && product["Sale Price"] === 0 ? (
                      <span className="price">Rs.{product.Price}</span>
                    ) : (
                      <>
                        <span className="price">
                          Rs.{product["Sale Price"]}
                        </span>
                        <span className="o-price">Rs.{product.Price}</span>
                        <span className="disc">
                          {" "}
                          (UPTO{" "}
                          {Math.round(
                            ((product["Price"] - product["Sale Price"]) /
                              product["Price"]) *
                              100
                          )}
                          % OFF)
                        </span>
                      </>
                    )}
                  </a>
                </div>
                <div className="pro-action-btns">
                  <div className="pro-btn-cart">
                    <Button
                      onClick={() => {
                        if (product && product["Type"] === "simple") {
                          this.props.addToCartAction(
                            product,
                            this.state.value || 1
                          );
                        } else {
                          this.props.addToCartAction(
                            getVariant(product["Varients"], [
                              this.state.color && this.state.color,
                              this.state.age && this.state.age,
                              this.state.size && this.state.size,
                            ]).variant,
                            this.state.value || 1
                          );
                        }
                      }}
                      className="btn-cart"
                    >
                      ADD
                    </Button>
                  </div>
                  <div className="pro-quick-view">
                    <a
                      onClick={() => {
                        this.setState({
                          view: true,
                          id: product && product["Id"],
                        });
                        this.handleShow();
                      }}
                    >
                      <div className="qv-img"></div>
                      <span>Quick View</span>
                    </a>
                  </div>
                  <div className="pro-shortlist">
                    <a
                      href="#a"
                      onClick={() => {
                        if (product && product["Type"] === "simple") {
                          this.props.saveProductToShortlist(product);
                          this.setState({
                            shortlist: !this.state.shortlist,
                          });
                        } else {
                          this.props.saveProductToShortlist(
                            product["Varients"][0]
                          );
                          this.setState({
                            shortlist: !this.state.shortlist,
                          });
                        }
                      }}
                    >
                      <div className={foundInCookies ? "sl-s-img" : "sl-img"}>
                        {" "}
                      </div>
                      <span>Shortlist</span>
                    </a>
                  </div>
                </div>
              </div>
            </Tab.Container>
            <ViewPopup
              handleShow={this.handleShow}
              product={product && product}
              id={product && product["Id"]}
              handleClose={this.handleClose}
              show={this.state.show}
              // addToCartAction={this.props.addToCartAction}
              saveToCart={this.handleShowCart}
            />
          </div>
        </div>

        <CartPopup
          handleShow={this.handleShowCart}
          handleClose={this.handleCloseCart}
          show={this.state.cartToggle}
        />
      </>
    );
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      // saveDataToCart: Actions.saveDataToCart,
      saveProductToShortlist: Actions.saveProductToShortlist,
    },
    dispatch
  );
};

export default withReducer(
  "productReducer",
  reducer
)(connect(null, mapDispatchToProps)(Product));
